* {
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Oswald', sans-serif;
    background-color: #fff;
}

header {
    height: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo {
    color: #000000;
    font-size: 36px;
    font-weight: 500;
    line-height: 53px;
    text-decoration: none;
    text-transform: uppercase;
}

main {
    background: #817070;
    display: flex;
    justify-content: center;
}
